html, body, .dropzone {
    height: 100%;
}

/* Header */
#header_nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    padding: 10px;
    background-color: rgb(240, 240, 240);;
    box-shadow: 0 0 10px rgba(0, 0, 0, .5);
}

#header_nav h1 {
    display: inline-block;
    font-size: 20px;
    margin: 0;
    vertical-align: middle;
    margin-right: 15px;
}

/* Dropzone */
.dropzone {
    padding-top: 54px;
}

.dropzone.dragging {
    background-color: #D2E4F8;
}

#upload_wrapper {
    text-align: center;
}

#upload_wrapper #drop_text {
    font-size: 60px;
    padding-top: 200px;
}

#upload_wrapper .fileUpload {
    position: relative;
    overflow: hidden;
    margin: 10px;
}

#upload_wrapper .fileUpload input {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    padding: 0;
    font-size: 20px;
    cursor: pointer;
    opacity: 0;
    filter: alpha(opacity=0);
}

#tool_wrapper {
    margin-bottom: 40px;
}

#tool_wrapper .table-container {
    overflow: auto;
}

#tool_wrapper .panel .panel-heading em {
    font-size: 10px;
    margin-left: 10px;
}

#tool_wrapper table select {
    max-width: 100px;
}


